@import url(https://fonts.googleapis.com/css?family=Sen:400,700,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body,h1,h2,h3,h4,div,input,a,span,button,textarea{font-family:'Sen', sans-serif !important}input,select,textarea{border-bottom:2px solid #dedede !important}body{background-color:#282c34 !important}.App{height:100%}.AppContext{padding-bottom:3em}.section{padding:3em 0}.section.Support-Info{padding:3em 2em;text-align:center;color:white;background:rgba(0,0,0,0.3);border-radius:.5em}.App-header{padding:2rem 0;color:white}.App-header .right.menu .item{color:white}.App-header .right.menu .item .button{margin-left:.3em !important}.App-header .logo{font-size:2.5em;font-weight:bold;display:block;color:white}.App-header .description{text-align:center}.App-link{color:#61dafb}#LockScreenPreview{position:relative;background:white;background-image:url(/lockscreenme/static/media/default-phone-bg.cd9fe36d.jpg);background-size:cover;background-repeat:no-repeat}#LockScreenPreview .section{padding:.5em 1em}#LockScreenPreview .section.personalinfo{color:white}#LockScreenPreview .theme{position:absolute;width:100%}#LockScreenPreview .theme.indicator{top:1%;right:2%;width:auto !important;font-size:small;color:white;border-radius:5em;padding:0 1em;background:rgba(0,0,0,0.8)}#LockScreenPreview .theme.datetime{text-align:center;top:10%;color:white}#LockScreenPreview .theme.datetime .time{font-size:3.5em;margin-bottom:0;line-height:1}#LockScreenPreview .theme.datetime .date{line-height:1}#LockScreenPreview .theme.infos{top:50%}#LockScreenPreview .theme.infos .personalinfo .name{font-weight:bold}#LockScreenPreview .theme.infos .personalinfo .bio{font-size:12px}#LockScreenPreview .theme.infos .contact{background-color:rgba(255,255,255,0.7)}footer{margin:3em 0 1em;text-align:center;line-height:1.2;color:grey}footer img{margin-bottom:.5em;height:2em}footer .logo{font-size:1.2em;font-weight:bold;color:rgba(255,255,255,0.7)}

